import "core-js/modules/es.array.push.js";
import "core-js/modules/web.url-search-params.delete.js";
import "core-js/modules/web.url-search-params.has.js";
import "core-js/modules/web.url-search-params.size.js";
import { mapGetters } from 'vuex';
import JSZip from 'jszip';
import DashboardTabs from '@/components/DashboardTabs.vue';
export default {
  data() {
    return {
      Number: 0,
      showLogoutButton: false,
      tableData: [],
      paginatedTableDataCache: [],
      search: '',
      currentPage: 1,
      pageSize: 15,
      selectedFiles: [],
      sortOption: 'dateDesc',
      isUploading: false,
      showdetailDialog: false,
      detailFile: null,
      activeUrlTab: 'originUrl',
      defaultUrlFormat: 'originUrl',
      showUrlDialog: false,
      useCustomUrl: 'false',
      // 是否启用自定义链接
      customUrlPrefix: '',
      // 自定义链接前缀
      loading: false,
      // 加载状态,
      showStyle: {
        flowerWater: false,
        //是否瀑布流
        vertical: false,
        // 竖屏
        landscape: false // 横屏
      }
    };
  },
  components: {
    DashboardTabs
  },
  computed: {
    ...mapGetters(['credentials', 'adminUrlSettings', 'userConfig']),
    sortIcon() {
      return this.sortOption === 'dateDesc' ? 'sort-amount-down' : 'sort-alpha-up';
    },
    dialogWidth() {
      return window.innerWidth > 768 ? '50%' : '90%';
    },
    accessType() {
      if (this.detailFile?.metadata?.ListType === 'White') {
        return '正常';
      } else if (this.detailFile?.metadata?.ListType === 'Block' || this.detailFile?.metadata?.Label === 'adult') {
        return '受限';
      } else {
        return '正常';
      }
    },
    allUrl() {
      return {
        'originUrl': `${this.rootUrl}${this.detailFile?.name}`,
        'mdUrl': `![${this.detailFile?.metadata?.FileName || this.detailFile?.name}](${this.rootUrl}${this.detailFile?.name})`,
        'htmlUrl': `<img src="${this.rootUrl}${this.detailFile?.name}" alt="${this.detailFile?.metadata?.FileName || this.detailFile?.name}" width=100%>`,
        'bbUrl': `[img]${this.rootUrl}${this.detailFile?.name}[/img]`,
        'tgId': this.detailFile?.metadata?.TgFileId || '未知',
        'S3Location': this.detailFile?.metadata?.S3Location || '未知'
      };
    },
    tableColumnNum() {
      return window.innerWidth > 768 ? 3 : 1;
    },
    tablePreviewSpan() {
      return window.innerWidth > 768 ? 2 : 1;
    },
    disableTooltip() {
      return window.innerWidth < 768;
    },
    selectPage() {
      // 如果当前页所有文件都被选中，则返回 true，否则返回 false
      return this.paginatedTableDataCache.every(file => file.selected);
    },
    selectedPageFiles() {
      // 如果当前页有文件被选中，则返回 true，否则返回 false
      return this.paginatedTableDataCache.some(file => file.selected);
    },
    selectPageIcon() {
      // 全选为 true 时，返回 check-square；部分选中为 minus-square；全不选为 square
      return this.selectPage ? 'check-square' : this.selectedPageFiles ? 'minus-square' : 'square';
    },
    rootUrl() {
      // 链接前缀，优先级：用户自定义 > urlPrefix > 默认
      return this.useCustomUrl === 'true' ? this.customUrlPrefix : this.userConfig?.urlPrefix || `${document.location.origin}/file/`;
    }
  },
  watch: {
    tableData: {
      handler(newData) {
        // selectedFiles 增加 newData中新选中，不包含在 selectedFiles 中的文件
        this.selectedFiles = this.selectedFiles.concat(newData.filter(file => file.selected && !this.selectedFiles.includes(file)));
        // selectedFiles 删掉 newData 中已取消选中的文件
        this.selectedFiles = this.selectedFiles.filter(file => file.selected);
      },
      deep: true
    },
    sortOption(newOption) {
      localStorage.setItem('sortOption', newOption);
    },
    defaultUrlFormat(newFormat) {
      localStorage.setItem('defaultUrlFormat', newFormat);
    },
    showdetailDialog(newVal) {
      if (newVal) {
        this.activeUrlTab = this.defaultUrlFormat || 'originUrl';
      }
    },
    customUrlPrefix(val) {
      this.$store.commit('setAdminUrlSettings', {
        key: 'customUrlPrefix',
        value: val
      });
    },
    useCustomUrl(val) {
      this.$store.commit('setAdminUrlSettings', {
        key: 'useCustomUrl',
        value: val
      });
    }
  },
  methods: {
    initObserver() {
      this.observer = new IntersectionObserver(entries => {
        if (entries[0].isIntersecting) {
          this.loadMoreData(false);
        }
      }, {
        threshold: 1.0
      });
      this.observer.observe(this.$refs.observer);
    },
    filteredTableData() {
      return this.tableData.filter(data => !this.search || data.name.toLowerCase().includes(this.search.toLowerCase()) || data.metadata?.FileName?.toLowerCase().includes(this.search.toLowerCase()));
    },
    paginatedTableData() {
      const sortedData = this.sortData(this.filteredTableData());
      // const start = (this.currentPage - 1) * this.pageSize;
      // const end = start + this.pageSize;
      // let data = sortedData.slice(start, end);
      // 增加previewSrcList属性，用于预览图片
      // const fullList = data.filter(file => !file.metadata?.FileType?.includes('video')).map(file => `/file/${file.name}?from=admin`);
      // data.forEach(file => {
      //     if (!file.metadata?.FileType?.includes('video')) {
      //         // 重新排序，索引大于等于当前索引的元素在前，否则在后
      //         file.previewSrcList = fullList.slice(fullList.indexOf(`/file/${file.name}?from=admin`)).concat(fullList.slice(0, fullList.indexOf(`/file/${file.name}?from=admin`)));
      //     }
      // });
      // 增加channelTag属性，用于显示渠道信息
      sortedData.forEach(file => {
        if (file.metadata?.Channel === 'TelegramNew') {
          file.channelTag = 'TG';
        } else if (file.metadata?.Channel === 'CloudflareR2') {
          file.channelTag = 'R2';
        } else if (file.metadata?.Channel === 'S3') {
          file.channelTag = 'S3';
        } else {
          file.channelTag = '未知';
        }
      });
      this.paginatedTableDataCache = sortedData;
    },
    handleChangeCss() {
      if (this.showStyle.landscape || this.showStyle.vertical) {
        this.showStyle.flowerWater = false;
      }
    },
    handleDownload(key) {
      const link = document.createElement('a');
      link.href = `/file/${key}?from=admin`;
      link.download = key;
      link.click();
    },
    openDetailDialog(index, key) {
      this.detailFile = this.paginatedTableDataCache[index];
      this.showdetailDialog = true;
    },
    handleTabClick(tab) {
      this.activeUrlTab = tab.props.name;
    },
    handleUrlClick(event) {
      // 复制到剪贴板
      navigator.clipboard.writeText(event.target.value).then(() => {
        this.$message({
          type: 'success',
          message: '复制成功'
        });
      }).catch(() => {
        this.$message({
          type: 'error',
          message: '复制失败'
        });
      });
    },
    handleDetailDelete(key) {
      this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.fetchWithAuth(`/api/manage/delete/${key}`, {
          method: 'GET'
        }).then(response => {
          if (response.ok) {
            const fileIndex = this.tableData.findIndex(file => file.name === key);
            if (fileIndex !== -1) {
              this.tableData.splice(fileIndex, 1);
            }
          } else {
            return Promise.reject('请求失败');
          }
        }).then(() => {
          this.updateStats(-1, false);
          this.$message.success('删除成功!');
          this.showdetailDialog = false;
        }).catch(() => this.$message.error('删除失败，请检查网络连接'));
      }).catch(() => this.$message.info('已取消删除'));
    },
    handleBlock(key) {
      this.$confirm('此操作将把该文件加入黑名单, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.fetchWithAuth(`/api/manage/block/${key}`, {
          method: 'GET'
        }).then(response => {
          if (response.ok) {
            const fileIndex = this.tableData.findIndex(file => file.name === key);
            if (fileIndex !== -1) {
              this.tableData[fileIndex].metadata.ListType = 'Block';
            }
          } else {
            return Promise.reject('请求失败');
          }
        }).then(() => {
          this.$message.success('加入黑名单成功!');
        }).catch(() => this.$message.error('加入黑名单失败，请检查网络连接'));
      }).catch(() => console.log('已取消加入黑名单'));
    },
    handleWhite(key) {
      this.$confirm('此操作将把该文件加入白名单, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.fetchWithAuth(`/api/manage/white/${key}`, {
          method: 'GET'
        }).then(response => {
          if (response.ok) {
            const fileIndex = this.tableData.findIndex(file => file.name === key);
            if (fileIndex !== -1) {
              this.tableData[fileIndex].metadata.ListType = 'White';
            }
          } else {
            return Promise.reject('请求失败');
          }
        }).then(() => {
          this.$message.success('加入白名单成功!');
        }).catch(() => this.$message.error('加入白名单失败，请检查网络连接'));
      }).catch(() => console.log('已取消加入白名单'));
    },
    async fetchWithAuth(url, options = {}) {
      // 开发环境, url 前面加上 /api
      // url = `/api${url}`;
      if (this.credentials) {
        // 设置 Authorization 头
        options.headers = {
          ...options.headers,
          'Authorization': `Basic ${this.credentials}`
        };
        // 确保包含凭据，如 cookies
        options.credentials = 'include';
      }
      const response = await fetch(url, options);
      if (response.status === 401) {
        // Redirect to the login page if a 401 Unauthorized is returned
        this.$message.error('认证状态错误，请重新登录');
        this.$router.push('/adminLogin');
        throw new Error('Unauthorized');
      }
      return response;
    },
    handleDelete(index, key) {
      this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.fetchWithAuth(`/api/manage/delete/${key}`, {
          method: 'GET'
        }).then(response => {
          if (response.ok) {
            const fileIndex = this.tableData.findIndex(file => file.name === key);
            if (fileIndex !== -1) {
              this.tableData.splice(fileIndex, 1);
            }
          } else {
            return Promise.reject('请求失败');
          }
        }).then(() => {
          this.updateStats(-1, false);
          this.$message.success('删除成功!');
        }).catch(() => this.$message.error('删除失败，请检查网络连接'));
      }).catch(() => this.$message.info('已取消删除'));
    },
    handleBatchDelete() {
      this.$confirm('此操作将永久删除选中的文件, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        const promises = this.selectedFiles.map(file => this.fetchWithAuth(`/api/manage/delete/${file.name}`, {
          method: 'GET'
        }));
        Promise.all(promises).then(results => {
          let successNum = 0;
          results.forEach((response, index) => {
            if (response.ok) {
              successNum++;
              const fileIndex = this.tableData.findIndex(file => file.name === this.selectedFiles[index].name);
              if (fileIndex !== -1) {
                this.tableData.splice(fileIndex, 1);
              }
            }
          });
          this.selectedFiles = [];
          this.updateStats(-successNum, false);
          this.$message.success('批量删除成功!');
        }).catch(() => this.$message.error('批量删除失败，请检查网络连接'));
      }).catch(() => this.$message.info('已取消批量删除'));
    },
    handleBatchCopy() {
      let tmpLinks = '';
      switch (this.defaultUrlFormat) {
        case 'originUrl':
          tmpLinks = this.selectedFiles.map(file => `${this.rootUrl}${file.name}`).join('\n');
          break;
        case 'mdUrl':
          tmpLinks = this.selectedFiles.map(file => `![${file.metadata?.FileName || file.name}](${this.rootUrl}${file.name})`).join('\n');
          break;
        case 'htmlUrl':
          tmpLinks = this.selectedFiles.map(file => `<img src="${this.rootUrl}${file.name}" alt="${file.metadata?.FileName || file.name}" width=100%>`).join('\n');
          break;
        case 'bbUrl':
          tmpLinks = this.selectedFiles.map(file => `[img]${this.rootUrl}${file.name}[/img]`).join('\n');
          break;
        case 'tgId':
          tmpLinks = this.selectedFiles.map(file => file.metadata?.TgFileId || 'none').join('\n');
          break;
        case 's3Location':
          tmpLinks = this.selectedFiles.map(file => file.metadata?.S3Location || 'none').join('\n');
          break;
      }
      const links = tmpLinks;
      navigator.clipboard ? navigator.clipboard.writeText(links).then(() => this.$message.success('批量复制链接成功~')) : this.copyToClipboardFallback(links);
    },
    copyToClipboardFallback(text) {
      const textarea = document.createElement('textarea');
      document.body.appendChild(textarea);
      textarea.style.position = 'fixed';
      textarea.style.clip = 'rect(0 0 0 0)';
      textarea.style.top = '10px';
      textarea.value = text;
      textarea.select();
      document.execCommand('copy');
      document.body.removeChild(textarea);
      this.$message.success('批量复制链接成功~');
    },
    handleCopy(index, key) {
      let text = '';
      switch (this.defaultUrlFormat) {
        case 'originUrl':
          text = `${this.rootUrl}${key}`;
          break;
        case 'mdUrl':
          text = `![${this.paginatedTableDataCache[index].metadata?.FileName || key}](${this.rootUrl}${key})`;
          break;
        case 'htmlUrl':
          text = `<img src="${this.rootUrl}${key}" alt="${this.paginatedTableDataCache[index].metadata?.FileName || key}" width=100%>`;
          break;
        case 'bbUrl':
          text = `[img]${this.rootUrl}${key}[/img]`;
          break;
        case 'tgId':
          text = this.paginatedTableDataCache[index].metadata?.TgFileId || 'none';
          break;
      }
      navigator.clipboard ? navigator.clipboard.writeText(text).then(() => this.$message.success('复制文件链接成功~')) : this.copyToClipboardFallback(text);
    },
    loadMoreData(showLoading = true) {
      this.loading = showLoading;
      const start = this.tableData.length;
      // this.fetchWithAuth(`/api/manage/list?start=${start}&count=20`, { method: 'GET' })
      //     .then(response => response.json())
      //     .then(data => {
      //         if (data.length === 0) {
      //             this.$message.info('没有更多数据了');
      //             return;
      //         }

      //         const moreData = data.map(file => {
      //             file.selected = false;
      //             return file;
      //         });
      //         this.tableData = this.tableData.concat(data);
      //         this.sortData(this.tableData);
      //     })
      //     .catch(() => this.$message.error('加载更多数据失败，请检查网络连接'))
      //     .finally(() => this.loading = false);
      let result = [];
      for (let index = 0; index < 15; index++) {
        this.tableData.push({
          "name": "pexels-josh-michaels-80899-28833005",
          "metadata": {
            "FileName": "pexels-josh-michaels-80899-28833005.jpg",
            "FileType": "image/jpeg",
            "FileSize": "0.86",
            "UploadIP": "8.210.60.87",
            "ListType": "None",
            "TimeStamp": 1740722159181,
            "Label": "None",
            "Channel": "CloudflareR2",
            "ChannelName": "R2_env"
          }
        });
      }
      this.sortData(this.tableData);
      console.log('loadmoreData', this.tableData.length);
      this.loading = false;
      this.paginatedTableData();
    },
    updateStats(num, init = false) {
      if (init) {
        this.fetchWithAuth('/api/manage/list?count=-1&sum=true', {
          method: 'GET'
        }).then(response => response.json()).then(data => {
          this.Number = data.sum;
        }).catch(() => this.$message.error('更新统计信息失败，请检查网络连接'));
      } else {
        this.Number += num;
      }
    },
    sort(command) {
      this.sortOption = command;
    },
    showType(command) {
      this.showStyle.flowerWater = true;
      this.showStyle.vertical = command == 'verticalScreen';
      this.showStyle.landscape = command == 'landscapeScreen';
      if (this.showStyle.flowerWater) {
        this.loadMoreData(true);
      }
    },
    sortData(data) {
      return this.sortOption === 'nameAsc' ? data.sort((a, b) => a.name.localeCompare(b.name)) : data.sort((a, b) => b.metadata.TimeStamp - a.metadata.TimeStamp);
    },
    handleVideoClick(event) {
      const videoElement = event.target;
      if (videoElement.requestFullscreen) {
        videoElement.requestFullscreen();
      } else if (videoElement.webkitRequestFullscreen) {
        // Safari/Old Chrome
        videoElement.webkitRequestFullscreen();
      } else if (videoElement.mozRequestFullScreen) {
        // Firefox
        videoElement.mozRequestFullScreen();
      } else if (videoElement.msRequestFullscreen) {
        // IE/Edge
        videoElement.msRequestFullscreen();
      }
    },
    handleLogout() {
      this.$store.commit('setCredentials', null);
      this.$router.push('/adminLogin');
    },
    handleSelectPage() {
      if (this.selectPage) {
        this.paginatedTableDataCache.forEach(file => file.selected = false);
      } else {
        this.paginatedTableDataCache.forEach(file => file.selected = true);
      }
    },
    showFullScreen() {
      this.$router.push('/fallImg');
    },
    handleBatchAction(command) {
      if (command === 'copy') {
        this.handleBatchCopy();
      } else if (command === 'delete') {
        this.handleBatchDelete();
      } else if (command === 'download') {
        this.handleBatchDownload();
      } else if (command === 'ban') {
        this.handleBatchBlock();
      } else if (command === 'white') {
        this.handleBatchWhite();
      }
    },
    handleBatchBlock() {
      this.$confirm('此操作将把选中的文件加入黑名单, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        const promises = this.selectedFiles.map(file => this.fetchWithAuth(`/api/manage/block/${file.name}`, {
          method: 'GET'
        }));
        Promise.all(promises).then(results => {
          results.forEach((response, index) => {
            if (response.ok) {
              const fileIndex = this.tableData.findIndex(file => file.name === this.selectedFiles[index].name);
              if (fileIndex !== -1) {
                this.tableData[fileIndex].metadata.ListType = 'Block';
              }
            }
          });
          this.$message.success('批量加入黑名单成功!');
        }).catch(() => this.$message.error('批量加入黑名单失败，请检查网络连接'));
      }).catch(() => this.$message.info('已取消批量加入黑名单'));
    },
    handleBatchWhite() {
      this.$confirm('此操作将把选中的文件加入白名单, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        const promises = this.selectedFiles.map(file => this.fetchWithAuth(`/api/manage/white/${file.name}`, {
          method: 'GET'
        }));
        Promise.all(promises).then(results => {
          results.forEach((response, index) => {
            if (response.ok) {
              const fileIndex = this.tableData.findIndex(file => file.name === this.selectedFiles[index].name);
              if (fileIndex !== -1) {
                this.tableData[fileIndex].metadata.ListType = 'White';
              }
            }
          });
          this.$message.success('批量加入白名单成功!');
        }).catch(() => this.$message.error('批量加入白名单失败，请检查网络连接'));
      }).catch(() => this.$message.info('已取消批量加入白名单'));
    },
    handleBatchDownload() {
      // 将选中文件打包成 zip 文件下载
      const zip = new JSZip();
      const folder = zip.folder('files');
      // 构造Promise数组，等待所有文件下载完成后再打包
      const fileNameCount = {}; // 用于跟踪文件名出现的次数

      const downloadPromises = this.selectedFiles.map(file => {
        return fetch(`/file/${file.name}?from=admin`).then(response => response.blob()).then(blob => {
          // 检查文件名是否已经存在
          let fileName = file.metadata?.FileName || file.name;
          if (fileNameCount[fileName]) {
            // 如果已经存在，则在文件名后加上编号
            const extension = fileName.substring(fileName.lastIndexOf('.'));
            const baseName = fileName.substring(0, fileName.lastIndexOf('.'));
            fileName = `${baseName}(${fileNameCount[fileName]})${extension}`;
            fileNameCount[file.name]++;
          } else {
            // 如果不存在，则初始化为1
            fileNameCount[fileName] = 1;
          }

          // 将文件添加到 zip 文件夹中
          folder.file(fileName, blob);
        });
      });
      Promise.all(downloadPromises).then(() => zip.generateAsync({
        type: 'blob'
      })).then(blob => {
        const link = document.createElement('a');
        link.href = URL.createObjectURL(blob);
        link.download = 'files.zip';
        link.click();
      });
    },
    isVideo(file) {
      let flag = file.metadata?.FileType?.includes('video') || file.metadata?.FileType?.includes('audio');
      // 用文件名后缀判断是否为视频文件
      if (!flag) {
        const videoExtensions = ['mp4', 'webm', 'ogg', 'avi', 'mov', 'flv', 'wmv', 'mkv', 'rmvb', '3gp', 'mpg', 'mpeg', 'm4v', 'f4v', 'rm', 'asf', 'dat', 'ts', 'vob', 'swf', 'divx', 'xvid', 'm2ts', 'mts', 'm2v', '3g2', '3gp2', '3gpp', '3gpp2', 'mpe', 'm1v', 'mpv', 'mpv2', 'mp2v', 'm2t', 'm2ts', 'm2v', 'm4b', 'm4p', 'm4v', 'm4r'];
        const extension = file.name.substring(file.name.lastIndexOf('.') + 1);
        flag = videoExtensions.includes(extension);
      }
      return flag;
    },
    isImage(file) {
      let flag = file.metadata?.FileType?.includes('image');
      // 用文件名后缀判断是否为图片文件
      if (!flag) {
        const imageExtensions = ['jpg', 'jpeg', 'png', 'gif', 'bmp', 'webp', 'svg', 'ico', 'tiff', 'tif', 'psd', 'ai', 'eps', 'raw', 'cr2', 'nef', 'orf', 'sr2', 'dng', 'arw', 'rw2', 'raf', 'pef', 'x3f', 'srf', 'erf', 'mrw', 'nrw', 'kdc', 'dcr', 'mef', 'mos', 'crw', 'raf', 'rwl', 'srw', '3fr', 'fff', 'iiq', 'qtk', 'bay', 'k25', 'kdc', 'dcs', 'drf', 'dng', 'erf', 'kdc', 'mdc', 'mef', 'mos', 'mrw', 'nef', 'nrw', 'orf', 'pef', 'ptx', 'pxn', 'r3d', 'raf', 'raw', 'rwl', 'rw2', 'rwz', 'sr2', 'srf', 'x3f'];
        const extension = file.name.substring(file.name.lastIndexOf('.') + 1);
        flag = imageExtensions.includes(extension);
      }
      return flag;
    },
    handlePageChange(page) {
      this.currentPage = page;
      // 到最后一页时，加载更多数据
      if (this.currentPage === Math.ceil(this.filteredTableData.length / this.pageSize)) {
        this.loadMoreData();
      }
    }
  },
